/* eslint-disable no-restricted-globals */
import jora from 'jora';
console.log('Worker is initializing');

export function executeQuery(data) {
  const { query, input } = data;
  console.log('Worker received:', { query, input });

  try {
    const compiledQuery = jora(query);
    const parsedInput = JSON.parse(input);
    const result = compiledQuery(parsedInput);
    console.log('Worker result:', result);
    self.postMessage(JSON.stringify({ result }));
  } catch (error) {
    console.error('Worker error:', error);
    self.postMessage(JSON.stringify({ error: (error).message }));
  }
}

self.onmessage = (event) => {
  console.log('Worker received:', event);
  const data = JSON.parse(event.data || '{}');
  if (data.name === 'executeQuery') {
    executeQuery(data);
  }
};